import React from 'react'
import { Card, Text } from 'theme-ui'
import Section from '@components/Section'
import useSiteMetadata from '../../../helpers/useMySiteMetadata'

const Commitment = props => {
  const { disclaimer } = useSiteMetadata()

  return (
  <Section aside title='免責聲明' {...props}>
    <Card variant='paper'>
      <Text variant='p'>
        {disclaimer}
      </Text>
    </Card>
  </Section>
)
}

export default Commitment
