import React from 'react'
import { Layout, Stack, Main, Sidebar } from '@layout'
import PageTitle from '@components/PageTitle'
import Divider from '@components/Divider'
import Seo from '@widgets/Seo'
import ContactForm from '@widgets/ContactForm'
import ContactInfo from '@widgets/ContactInfo'
import Commitment from '@widgets/Commitment'
// Michael Lu, 2024/10/6
import { graphql } from 'gatsby'

export const query = graphql`
  query MobileMenuQuery {
    allArticleCategory(
      filter: { hideCat: { ne: "true" } }
      sort: { catOrder: ASC }
    ) {
      nodes {
        catOrder
        hideCat
        name
        slug
      }
    }
    allArticleTag {
      nodes {
        id
        name
        slug
      }
    }
  }
`

const PageContact = props => {
  // console.log('PageContact props=', props)
  const catItems = props.data.allArticleCategory.nodes
  const tagItems = props.data.allArticleTag.nodes
  // console.log('PageContact catItems=', catItems, 'tagItems=', tagItems)
  const mobileMenu = []
    .concat(
      catItems && catItems.length > 0
        ? [
            {
              title: '主題',
              items: catItems
            }
          ]
        : []
    )
    .concat(
      tagItems && tagItems.length > 0
        ? [
            {
              title: '標籤',
              items: tagItems
            }
          ]
        : []
    )
  // console.log('PageContact, mobileMenu=', mobileMenu)
  props.pageContext.mobileMenu = mobileMenu
  // console.log('PageContact props2=', props)
  
  return (
    <Layout {...props}>
      <Seo title='聯絡我們' />
      <Divider />
      <Stack>
        <Main>
          <PageTitle
            header='聯絡我們'
            subheader='請填寫下表，我們將盡快回覆，謝謝。'
          />
          <Divider />
          <ContactForm />
        </Main>
        <Sidebar>
          <Commitment />
          <Divider />
          <ContactInfo />
        </Sidebar>
      </Stack>
    </Layout>
  )
}

export default PageContact
